import tabControl from '@/components/tabControl-tag/tabControl-tag.vue';
import {
		mapState
	} from 'vuex';
import md5 from '../../kayida/common/md5.js'
import easyLoadimage from '@/components/easy-loadimage/easy-loadimage.vue'
const MockData = require('@/static/easy-loadimage/mock-data.json')

export default {
	components: {tabControl,easyLoadimage},
	computed: {
	            ...mapState(['userInfo','is_bind'])  
	        },
	data() {
		return {
			respects: '',
			infor_title: '',
			current: 0,
			infor_current:0,
			infor_card: [],
			poster:'',
			val:"",
			share:0,
			pageNo:1,
			share_index:'',
			scrollTop:0,
			old:{
				scrollTop:0
			},
			list:MockData
		};
	},
	onPageScroll({scrollTop}) {
		// 传入scrollTop值并触发所有easy-loadimage组件下的滚动监听事件
		this.scrollTop = scrollTop;
	},
	onPullDownRefresh() {
		let data = {
			user_id: this.userInfo.user_id,
			token: this.userInfo.token,
			sign: md5(this.userInfo.user_id+this.userInfo.token)
		}
		this.request.httpRequest('api/timing/index',data,'GET').then(res =>{
			if(res.data.code === 1000){
				this.respects = res.data.retval
				uni.stopPullDownRefresh();
			}
			}
		)
	},
	onLoad(option) {
		let data = {
			user_id: this.userInfo.user_id,
			token: this.userInfo.token,
			sign: md5(this.userInfo.user_id+this.userInfo.token)
		}
		this.request.httpRequest('api/timing/index',data,'GET').then(res =>{
			if(res.data.code === 1000){
				this.respects = res.data.retval
				uni.startPullDownRefresh()
			}
			}
		)
		this.request.httpRequest('api/poster/index','GET').then(res =>{
			if(res.data.code === 1000){
				this.poster = res.data.retval
			}
		});
	},
	onHide() {
		this.share = 0
	},
	watch:{
		current(newval,oldval) {
			if(newval === 1) {
				this.infor_current = 0;
				this.request.httpRequest('api/newsnav/index','GET').then(res =>{
					if(res.data.code === 1000){
						this.infor_title = res.data.retval.nav
					}
				});
				let data = {
					nav_id:newval - 1,
					user_id: this.userInfo.user_id,
					token: this.userInfo.token,
					sign: md5(this.userInfo.user_id+this.userInfo.token)
				}
				this.request.httpRequest('api/news/index',data,'GET').then(res =>{
					if(res.data.code === 1000){
						this.infor_card = res.data.retval
					}
				});
			}else if(newval === 2){
				this.request.httpRequest('api/poster/index','GET').then(res =>{
					if(res.data.code === 1000){
						this.poster = res.data.retval
					}
				});
			}else if(newval === 0) {
				let data = {
					user_id: this.userInfo.user_id,
					token: this.userInfo.token,
					sign: md5(this.userInfo.user_id+this.userInfo.token)
				}
				this.request.httpRequest('api/timing/index',data,'GET').then(res =>{
					if(res.data.code === 1000){
						this.respects = res.data.retval
					}
					}
				)
			}
		},
		infor_current(newval,oldval) {
			let data = {
				nav_id:newval,
				user_id: this.userInfo.user_id,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/news/index',data,'GET').then(res =>{
				if(res.data.code === 1000){
					this.infor_card = res.data.retval
				}
			});
		}
	},
	methods: {
		lower:function(e) {
			this.pageNo = ++this.pageNo
			let data = {
				pageNo:this.pageNo,
				user_id: this.userInfo.user_id,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/timing/index',data,'GET').then(res =>{
				if(res.data.code === 1000){
					let list = res.data.retval
					this.respects = this.respects.concat(list)
				}
				}
			)
		},
		scroll:function(e) {
		},
		shareWeixin() {
			uni.share({
			    provider: "weixin",
			    scene: "WXSceneSession",
			    type: 2,
			    imageUrl: this.respects[this.share_index].thumb,
			    success: function (res) {
			        uni.showToast({
			        	title:"分享成功",
			        	icon:"none"
			        });
			    },
			    fail: function (err) {
			        uni.showToast({
			        	title:"分享失败",
			        	icon:"none"
			        });
			    }
			});
			this.share = 0
		},
		shareFriends() {
			uni.share({
			    provider: "weixin",
			    scene: "WXSenceTimeline",
			    type: 2,
			    imageUrl: this.respects[this.share_index].thumb,
			    success: function (res) {
			        uni.showToast({
			        	title:"分享成功",
			        	icon:"none"
			        });
			    },
			    fail: function (err) {
			        uni.showToast({
			        	title:"分享失败",
			        	icon:"none"
			        });
			    }
			});
			this.share = 0
		},
		shareDetail(index) {
				this.share = 1
				this.share_index = index
				uni.setClipboardData({
				    data: this.respects[this.share_index].content,
				    success: function () {
						uni.showToast({
							title: '复制成功',
							duration:2000,
							icon:"none"
						})
				    }
				});
		},
		shareCancel() {
			this.share = 0
		},
		toNewsDetail(index) {
			uni.navigateTo({
				url:"../newsdetail/newsdetail?index="+index+"&infor="+this.infor_current
			})
		},
		toRespects() {
			if(this.current !== 0){
				this.current = 0
			}
		},
		toInfor() {
			if(this.current !== 1){
				this.current = 1
			}
		},
		toGener() {
			if(this.current !== 2){
				this.current = 2
			}
		},
		toInforC(index) {
			if(this.infor_current !== index){
				this.infor_current = index
			}
		},
		toPromotion(index) {
			uni.navigateTo({
				url:"../promotioncenter/promotioncenter?id="+index,
			});
		},
	}
}
